import { Component, OnInit, Injector, HostListener, ViewChild, TemplateRef } from '@angular/core';

import { MatDialog } from '@angular/material/dialog';
import { MatDialogRef } from "@angular/material/dialog";

import { MoveInOutService } from '../../../../api/services/move-in-out.service';
import { UserService } from '../../../../api/services/user.service';
import { ApartmentService } from '../../../../api/services/apartment.service';
import { LookupService } from '../../../../api/services/lookup.service';
import { SharedService } from '../../../../shared/services/shared.service';
import { ModalService } from '../../../../shared/services/modal.service';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';
import * as moment from 'moment';

@Component({
  selector: 'app-movein-maintain',
  templateUrl: './movein-maintain.component.html',
  styleUrls: ['./movein-maintain.component.scss']
})
export class MoveinMaintainComponent implements OnInit {

  isDataLoaded: boolean = false;

  moveInData: string = "";
  moveInDataList: any;

  ItemStartIndex: any = 0;
  ItemEndIndex: any;
  totalItems: any;
  itemLimit: any = 10;

  unitFieldType: string = "unitno";
  unitOrder: boolean = false;

  isMobile: boolean = false;

  apartmentBlockId: any;
  apartmentBlockUnitId: any;

  blockUnitData: any;

  modalService:any;

  isMoveInSubmitted: boolean = true;

  isError: boolean = false;
  alertMessage: string = "";

  selectedInput: string = "";
  columnField: any = {}

  constructor(
    private injector: Injector,
    private moveInOutService: MoveInOutService,
    private userService: UserService,
    private apartmentService: ApartmentService,
    private lookupService: LookupService,
    private sharedService: SharedService,
    private cookieService: CookieService,
    private dialog: MatDialog
  ) { 
    this.modalService = this.injector.get(ModalService);
  }

  getIndexParams(event) {
    this.ItemStartIndex = event.ItemStartIndex;
    this.ItemEndIndex = event.ItemEndIndex;
    this.itemLimit = event.itemLimit;
  }

  sortUnitData(type) {
    this.unitFieldType = type;
    this.unitOrder = !this.unitOrder;
  }

  getFieldOrderBy(type) {
    if(this.unitFieldType == type){
      return this.unitOrder ? 'asc' : 'desc';
    }
    else return '';
  }

  selectColInput(value) {
    this.selectedInput = value;
  }

  onDateChange(event, type) {

    if (event != null) {
      this.selectedInput = type;
      this.columnField[type] = moment(event).format("DD/MM/YYYY");
    }
    else {
      this.columnField = {};
    }

  }

  getDate(movein, date) {

    this.moveInDataList.map(obj => {
      if (obj.id == movein.id) {
        obj.inDateData = moment(date).format("DD/MM/YYYY");
      }
    });

    return moment(date).format("DD/MM/YYYY");
  }

  getTime(time) {
    var timeData = moment(time, 'HH:mm:ss').toDate();
    return moment(timeData).format('HH:mm A');
  }

  isMobileView() {
    return window.innerWidth <= 767 ? 'table-responsive' : '';
  }

  isItemsAvailable() {
    return this.totalItems > 0 ? true : false;
  }

  isNoItemsAvailable() {
    return this.totalItems == 0 ? true : false;
  }


  getBlockNo(item, id) {
    var data = _.filter(this.blockUnitData, item => {
      if (item.apartmentBlockUnitId === id)
        return item;
    });

    if (data === undefined || data.length == 0) {
      return '';
    }
    else {

      this.moveInDataList.map(obj => {
        if (obj.id == item.id) {
          obj.apartmentBlockNumber = data[0].apartmentBlockNumber;
        }
      });

      return data[0].apartmentBlockNumber;
    }
  }

  getBlockUnitNo(item, id) {
    var data = _.filter(this.blockUnitData, item => {
      if (item.apartmentBlockUnitId === id)
        return item;
    });

    if (data === undefined || data.length == 0) {
      return '';
    }
    else {

      this.moveInDataList.map(obj => {
        if (obj.id == item.id) {
          obj.apartmentBlockUnitNumber = data[0].apartmentBlockUnitNumber;
        }
      });

      return data[0].apartmentBlockUnitNumber;
    }
  }


  getApproveStatus(id) {
    switch (id) {
      case 372:
        return 'high';
        break;
      case 373:
        return 'low';
        break;
      case 374:
        return 'm-yellow';
        break;
      case 380:
        return 'grey-850';
        break;
    }
  }

  approveMoveIn(data) {

    var moveInData = data;
    moveInData.approvedBy = parseInt(this.cookieService.get('userId'));
    moveInData.approvedDate = new Date().toISOString();

    this.isMoveInSubmitted = false;

    this.moveInOutService.updateMoveIn(moveInData).subscribe((res: any) => {

      if (res.message) {
        this.isMoveInSubmitted = true;
        this.sharedService.setAlertMessage("Movein approved successfully");
      }
      else {
        this.isMoveInSubmitted = true;
        this.isError = true;
        this.alertMessage = res.errorMessage;
      }

    },
      error => {
        this.isMoveInSubmitted = true;
      });


  }

  ngOnInit() {



    this.moveInOutService.getMoveInByApartmentId(parseInt(this.cookieService.get('apartmentId'))).subscribe((res: any) => {

      this.moveInDataList = res.filter(item => {
        return item.isActive
      });

      this.totalItems = this.moveInDataList.length;

      if (this.totalItems > this.itemLimit) {
        this.ItemEndIndex = this.itemLimit;
      }
      else {
        this.ItemEndIndex = this.totalItems;
      }

      this.isDataLoaded = true;

    });

    this.apartmentService.getAllApartmentBlockUnits().subscribe((res: any) => {

      this.blockUnitData = res;

    });

  }

  showMoveInDetails(details): void {
    this.modalService.showMoveInDetails(details);
  }

  moveInUser(data) {

  }

}
